@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    @include m.contenedor;

    &-imagenes{
        @include m.grid(1, 2rem);

        @include m.tel{
            @include m.grid(2, 2rem)
        }

        @include m.tablet{
            @include m.grid(3, 2rem)
        }

        @include m.desk{
            @include m.grid(4, 2rem)
        }

        img{
            height: 20rem;
            object-fit: cover;
        }
    }
}
.modal{
    position: fixed;
    inset: 0;
    background-color: rgba(v.$negro, 0.5);
    display: grid;
    place-items: center;
    
    animation: fadeIn ease-in 0.5s;
    
    &.fade-out{
        animation: fadeOut ease-in 0.5s;
    }

    img{
        max-width: 100rem;
    }
}

@keyframes fadeIn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeOut {
    0%{
        opacity: 100;
    }
    100%{
        opacity: 0;
    }
}
.btn-cerrar{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    background-color: v.$blanco;
    border: none;
    display: grid;
    place-items: center;
    font-weight: 700;
    cursor: pointer;

    &:hover{
        background-color: v.$rosa;
    }
}