@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup{
    background-color: v.$rosa;
    h3,
    .dia{
        text-align: center;
        color: v.$blanco;
    }
    .dia{
        font-size: 3rem;
        text-transform: uppercase;
    }
}
.escenarios-contenedor{
    @include m.contenedor;
    @include m.tablet{
        @include m.grid(2, 5rem);
    }
}

.escenario{
    padding-top: 2rem;
    .nombre-escenario{
        font-weight: 900;
        font-size: 3rem;
        text-align: center;
        text-transform: uppercase;
        color: v.$negro;
    }
}

.calendario{
    @include m.reset-lista;

    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}
.bg-amarillo{
    background-color: v.$amarillo;
    li:nth-child(odd){
        background-color: lighten(v.$amarillo, 15%);
    }
}   
.bg-verde{
    background-color: v.$verde;
    li:nth-child(odd){
        background-color: lighten(v.$verde, 5%);
    }
}